h2 {
    border-bottom: 1px solid black;
    font-size: large;
    text-align: center;
}

ul{
    margin: 0em;
    padding: 0em;
}
.container {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.chat {
    flex: 1;
    font-size: large;
    font-family: "Times New Roman", Times, serif;
    overflow-y: scroll;
    border: 1px solid black;
    margin-bottom: 3.1em;
}

.message_list li{
    list-style-type: none;
    margin: 0.5em;
}
.msg{
    color: green;
}
.my-msg{
    color: blue;
}

.status-item{
    color: red;
}
.login-modal {
    background-color: rgba(0, 0, 0, 0);
    /*height: 100%;*/
    margin: auto;
    width: 50%;
    padding-top: 50px;
    text-align: center;
}
.login-page{
  margin-left: auto;
  text-align: center;
}

.message {
    border: 2px solid grey;
    border-radius: 0.5em;
    font-size: larger;
    min-height: 2em;
    margin: 0 0.5em 0.5em 0.5em;
    padding-left: 0.5em;
    position: fixed;
    bottom: 0;
    width: 95%;
}
.message:focus {
    outline: none;
}
.title {
    border-bottom: 3px solid black;
    color: green;
    font-size: xx-large;
    text-align: center;
}
.connected {
    margin: auto;
}
.user_window {
    border: 1px solid black;
    margin-bottom: 3.5em;
    width: 10em;
    margin-left: 0;
}
.users li {
    list-style-type: none;
    text-align: center;
}
.window {
    display: flex;
    flex: 1;
    margin: 0 0 0 0;
    min-height: 0;
}